<template>
  <v-content>
    <v-container fluid class="flex-xl-grow-1" width="125rem">
      <v-row class="rowSelectores">

        <!-- SELECTOR DE MES INICIO -->
        <v-col cols="2">
          <v-menu v-model="menu1" :close-on-content-click="false">
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                :value="mesSeleccionado"
                label="Mes"
                light
                readonly
                v-model="date"
                v-bind="attrs"
                v-on="on"
                class="textFieldSelectorMes"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="date"
              type="month"
              locale="es"
              class="datePicker"
            >
              <v-spacer></v-spacer>
              <v-btn text color="primary" >Cancel</v-btn>
              <v-btn text color="primary" @click="refresHours()">OK</v-btn>
            </v-date-picker>
          </v-menu>
        </v-col>
        <!-- SELECTOR DE MES FIN -->

        <!-- SELECTOR DE USUARIO INICIO -->
        <v-col cols="2">
          <v-select
            v-model = "usuarioSeleccionado"
            :items="usuariosDescripciones"
            label="Usuario"
            placeholder="Seleccionar"
            class="seleccionarUsuario"
            @input="nuevoUsuarioAsignado"
            dense
            outlined
          ></v-select>
        </v-col>
        <!-- SELECTOR DE USUARIO FIN -->

        <!-- SELECTOR DE ASIGNACIONES INICIO -->
        <v-col cols="1">
          <!-- <v-select
            multiple
            label="Proyecto"
            placeholder="Seleccionar"
            class="seleccionarProyecto"
            outlined
            dense
          > </v-select> -->
            <treeselect
              class="seleccionarProyecto"
              :multiple="true"
              :options="options"
              placeholder="Seleccionar"
              v-model="value"
              />
            <treeselect-value :value="value" class="seleccionarProyecto"/>
        </v-col>
        <!-- SELECTOR DE ASIGNACIONES FIN -->

        <!-- SELECTOR DE MODALIDADES INICIO -->
        <v-col cols="1">
          <v-select
            dense
            chips
            outlined
            :items="modalidadesDescripciones"
            label="Modalidad"
            placeholder="Seleccionar"
            class="seleccionarModalidad"
          ></v-select>  
        </v-col>
        <!-- SELECTOR DE MODALIDADES FIN -->

        <!-- BOTON DE GUARDAR INICIO -->
        <v-col cols="1">
          <v-btn color="#2991C6" @click="" class="btnGuardar"
            >Guardar</v-btn
          >
        </v-col>
        <!-- BOTON DE GUARDAR FIN -->

        <!-- BOTON DE CANCELAR INICIO -->
        <v-col cols="1">
          <v-btn
            color="#ffa025"
            dark
            @click="refresHours()"
            class="btnCancelar"
            >Cancelar</v-btn
          >
        </v-col>
        <!-- BOTON DE CANCELAR FIN -->
      </v-row>
      <v-row>
        <!-- BOTON EXPORTAR PDF INICIO -->
        <v-col cols="1">
          <v-btn
            color="#F40F02"
            @click="meterNumerosLocal()"
            dark
            class="btnExportarPDF"
            v-b-tooltip.hover
            title="Exportar a PDF"
          >
            <v-icon>mdi-file-pdf-outline</v-icon>
          </v-btn>
        </v-col>
        <!-- BOTON EXPORTAR PDF FIN -->

        <!-- BOTON EXPORTAR INICIO -->
        <v-col cols="1">
          <v-btn
            color="#1d6f42"
            @click="meterNumerosLocal()"
            dark
            class="btnExportar"
            v-b-tooltip.hover
            title="Exportar"
          >
            <v-icon>mdi-table-arrow-up</v-icon>
          </v-btn>
        </v-col>
        <!-- BOTON EXPORTAR FIN -->

        <!-- BOTON IMPORTAR INICIO -->
        <v-col cols="1">
          <v-btn
            color="#1d6f42"
            @click="meterNumerosLocal()"
            dark
            class="btnImportar"
            v-b-tooltip.hover
            title="Importar"
          >
            <v-icon>mdi-table-arrow-down</v-icon>
          </v-btn>
        </v-col>
        <!-- BOTON IMPORTAR FIN -->

        <!-- BOTON CARGA MASIVA INICIO -->
        <v-col cols="1">
          <v-btn
            color="#1a5e86"
            @click="meterNumerosLocal()"
            dark
            class="btnCargaMasiva"
            v-b-tooltip.hover
            title="Carga Masiva"
          >
            <v-icon>mdi-upload</v-icon>
          </v-btn>
        </v-col>
        <!-- BOTON CARGA MASIVA FIN -->

        <!-- BOTON REPORTES INICIO -->
        <v-col cols="1">
          <v-btn
            color="#1a5e86"
            @click="meterNumerosLocal()"
            dark
            class="btnReporte"
            v-b-tooltip.hover
            title="Reporte"
          >
            <v-icon>mdi-table</v-icon>
          </v-btn>
        </v-col>
        <!-- BOTON REPORTAR FIN -->
      </v-row>
      
      <!-- DATA TABLE INICIO -->
      <div>
        <b-table-simple
          hover
          small
          :borderless="borderless"
          caption-top
          responsive
          class="table table-hover"
        >
          <colgroup>
            <col />
            <col />
          </colgroup>
          <colgroup>
            <col />
            <col />
            <col />
          </colgroup>
          <colgroup>
            <col />
            <col />
          </colgroup>

          <b-thead class="tHead">
            <b-tr>
              <b-th colspan="4"></b-th>
              <!-- Iterador de numero de dias en mes -->
              <b-th v-for="day in days" class="numeroDia">{{ day }}</b-th>
              <!-- Ultima columna de Total Horas (sumas) -->
              <b-th colspan="2" class="totalHorasY">Total</b-th>
            </b-tr>
            <b-tr>
              <!-- Iterador de nombre de numero de dias en mes -->
              <b-th colspan="4"></b-th>
              <b-th v-for="day in dayName" class="nombreDia">{{ day }}</b-th>
              <b-th></b-th>
            </b-tr>
          </b-thead>

          <!-- TABLA ASIGNACIONES INICIO -->
          <b-tbody v-for="asignacion in asignacionesHoras">
            <b-tr>
              <b-th rowspan="3" colspan="1" class="color1"></b-th>
              <b-th rowspan="3" colspan="2" class="color1">{{ asignacion.name }}</b-th>
              <b-th class="colorLocal1">{{ asignacion.modalidades.local.name }}</b-th>

            <!-- LOCAL INICIO -->
              <b-td
                contenteditable="true"
                class="colorLocal1"
                v-for="(hora, index) in asignacion.modalidades.local.horas"
                v-model="hora.value"
                > {{hora}} </b-td>
              <b-td id="totalHoras" class="sumas"></b-td>
            </b-tr>
            <!-- LOCAL FIN -->

            <!-- HOME OFFICE INICIO -->
            <b-tr>
              <b-th class="colorHO1">
                {{asignacion.modalidades.homeoffice.name}}
                </b-th>
              <b-td
                contenteditable="true"
                class="colorHO1"
                v-for="(hora, index) in asignacion.modalidades.homeoffice.horas"
                >{{ hora }}</b-td>
              <b-td id="totalHoras" class="sumas">
               </b-td>
            </b-tr>
            <!-- HOME OFFICE FIN -->

            <!-- EXTRANJERO INICIO -->
            <b-tr>
              <b-th class="colorExtranjero1">{{ asignacion.modalidades.extranjero.name }}</b-th>
              <b-td
                contenteditable="true"
                class="colorExtranjero1"
                v-for="(hora, index) in asignacion.modalidades.extranjero.horas"
                >{{ hora }}</b-td>
              <b-td id="totalHoras" class="sumas"></b-td>
            </b-tr>
            <!-- EXTRANJERO FIN -->
          </b-tbody>
          
          <!-- FOOTER TABLA INICIO -->
          <b-tfoot>
            <b-tr>
              <b-td colspan="4" class="totalHorasX">
                Total
                <b></b>
              </b-td>
              <b-td v-for="day in days" class="sumas">0</b-td>
              <b-td class="sumaTotalMes">0</b-td>
            </b-tr>
          </b-tfoot>
          <!-- FOOTER TABLA FIN -->

          <!-- TABLA ASIGNACIONES FIN -->
        </b-table-simple>
        <!-- DATA TABLE FIN -->

        <!-- BOTONES DEBUG INICIO -->
        <!-- <v-btn @click="printAsignacionesHoras()"> Clg AsignacionesHoras  </v-btn> -->
        <v-btn @click="reiniciarValores()"> CLG generateJSONAsignacion</v-btn>
        <!-- BOTONES DEBUG FIN -->
      </div>
    </v-container>
  </v-content>
</template>

<script>
import moment, { months } from "moment";
import axios from "axios";
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
const ip = require('../../ip/ip')

export default {
  components: { Treeselect },
  data() {
    return {
      // bordes de grilla
      borderless: true,

      // Harcodeo de de carga de horas en 0
      inputrow: 0,

      // Array de numero de dias
      days: [],

      // Array de primera letra de cada dia (Popular con MomentJs)
      dayName: [],

      proyectos: [],

      // Nombre de proyectos
      proyectosDescripciones: [],

      usuarios : [],
      
      modalidades: [],

      modalidadesDescripciones: [],

      // Nombre de usuarios
      Usuario_Nombre_Completoes: [],

      // Descripciones de Usuarios
      usuariosDescripciones: [],

      // Key de usuario logeado
      usuario: "",

      // Usuario seleccionado , por defecto el mismo que el logeado
      usuarioSeleccionado : "",

      // Asignaciones 
      asignacionesHoras: [],
      asignacion: {},
      
      // Selector de mes
      date: new Date().toISOString().substr(0, 7),
      menu1: false,
      mesActualDinamico: 0,

      dataGet: [],
      
      day : 0,

      // Treeview selector de asignaciones
      value: [],
      options: [ {
      id: 'YPF',
      label: 'YPF',
      children: [ {
        id: 'YPF - 1',
        label: 'YPF - DATA MART VENTAS',
        isNew: true,
      }, {
        id: 'YPF - 2',
        label: 'YPF - BI',
      }, {
        id: 'YPF - 3',
        label: 'YPF - DATAWAREHOUSE',
      }, {
        id: 'YPF - 4',
        label: 'YPF - Desarrollos SAP',
      }, {
        id: 'YPF - 5',
        label: 'YPF - Desarrollos HANA',
      } ],
      }, 
      {
      id: 'desarrollos Internos',
      label: 'Desarrollos Internos',
      children: [ {
        id: 'Frontend - Vue.js',
        label: 'Frontend - Vue.js',
      }, {
        id: 'Backend - Nodejs',
        label: 'Backend - Nodejs',
      }, {
        id: 'Deploy',
        label: 'Deploy',
      },],
    } ],
      

    };
  },

  computed: {
    // Script de selector de mes
    MesSeleccionado() {
      return this.date ? moment(this.date).format("YYYY MM") : "";
    },
  },

  methods: {
    AsignacionesHoras(){
      console.log(this.asignacion.modalidades.local)
    },

    printAsignacionesHoras(){
      console.log(this.asignacionesHoras[0].modalidades.local.horas[0])
      var p = this.asignacionesHoras[0].modalidades.local.horas.map(h => h.valor.data)
      console.log(p)
    },

    reiniciarValores(){
      // Reemplazar this.asignacionesHoras[0].modalidades.local.horas por un for que agarre todas las asignaciones de la grilla 
      console.log(this.asignacionesHoras[0].modalidades.local.horas)
      this.asignacionesHoras[0].modalidades.local.horas.fill(0)
      console.log(this.asignacionesHoras[0].modalidades.local.horas);
    },

    nuevoUsuarioAsignado(){
      var usuarioDescripcion = this.usuarioSeleccionado
      var key = this.usuarios.filter( u => u.Usuario_Nombre_Completo == this.usuarioSeleccionado)[0].Usuario_Key
      this.usuarioSeleccionado = key
      this.giveMeHours();
      this.usuarioSeleccionado = usuarioDescripcion;
    },

    refresHours(){
      this.asignacionesHoras = []
      this.giveMeHours();
    },
  
     async eliminarMes() {
     await axios.delete(ip+"/horas/"+this.usuarioSeleccionado+'/'+this.mesActualDinamico).then((response) => {
        console.log(response.data);
      });
      console.log("pase");
    },

    MesActualFormato() {
      this.mesActualDinamico = moment(this.date).format("YYYYMM");
    },

    // Force Updates
    reload() {
      this.$forceUpdate();
    },

    // Script de extension de fechas de MomentJS
    moment: function (mesSeleccionado) {
      return moment(this.MesSeleccionado);
    },
 

    // sumasEnXLocal() {
    //   var sum = 0;
    //   for (var i = 0; i < this.asignacionesHoras[0].modalidades.local.horas.length;i++) {
    //     sum += parseInt(this.asignacionesHoras[0].modalidades.local.horas[i].valor.data);
    //   }
    //   console.log(sum);
    //   return sum;
    // },

    sumasEnXHomeOffice() {
      var sum = 0;
      for (
        var i = 0;
        i < this.asignacionDev1.modalidades.homeoffice.horas.length;
        i++
      ) {
        sum += this.asignacionDev1.modalidades.homeoffice.horas.hora[i];
      }
      return sum;
    },

    sumasEnXExtranjero() {
      var sum = 0;
      for (
        var i = 0;
        i < this.asignacionDev1.modalidades.extranjero.horas.length;
        i++
      ) {
        sum += this.asignacionDev1.modalidades.extranjero.horas.hora[i];
      }
      return sum;
    },


    persistencia() {
      this.eliminarMes();
      for(var i  = 0; i < this.asignacionesHoras.length ; i++ ){
      var local = this.persistenciaModalidadLocal(this.asignacionesHoras[i]);
      var homeoffice = this.persistenciaModalidadHomeOficce(this.asignacionesHoras[i]);
      var extranjero = this.persistenciaModalidadExtranjero(this.asignacionesHoras[i]);
      for (var i = 0; i < local.length; i++) {
        this.saveAsignation(local[i]);
        this.saveAsignation(homeoffice[i]);
        this.saveAsignation(extranjero[i]);
      }
      }
    },

    persistenciaModalidadHomeOficce(asig) {
      var homeoffice = [];
      var mesActual = this.mesCalculo();

      for (var i = 0; i < asig.modalidades.homeoffice.horas.length; i++) {
        homeoffice.push(
          this.armarPersistencia(
            asig.key_name,
            asig.modalidades.homeoffice.homeoffice_key,
            2,
            asig.modalidades.homeoffice.horas.hora[i],
            mesActual + i
          )
        );
      }
      return homeoffice;
    },

    persistenciaModalidadLocal(asig) {
      var local = [];
      var mesActual = this.mesCalculo();

      for (var i = 0; i < asig.modalidades.local.horas.length; i++) {
        local.push(
          this.armarPersistencia(
            asig.key_name,
            asig.modalidades.local.local_key,
            2,
            asig.modalidades.local.horas.hora[i],
            mesActual + i
          )
        );
      }
      return local;
    },

    persistenciaModalidadExtranjero(asig) {
      var extranjero = [];
      var mesActual = this.mesCalculo();

      for (var i = 0; i < asig.modalidades.extranjero.horas.length; i++) {
        extranjero.push(
          this.armarPersistencia(
            asig.key_name,
            asig.modalidades.extranjero.extranjero_key,
            2,
            asig.modalidades.extranjero.horas.hora[i],
            mesActual + i
          )
        );
      }
      return extranjero;
    },

    mesCalculo() {
      var mes = this.date;
      mes = this.date ? moment(this.date).format("YYYYMM") : "";
      mes = String(mes) + "01";
      mes = Number(mes);
      return mes;
    },

    armarPersistencia(proyecto, modalidad, usuario, cantidad, tiempo) {
      var asignacionPersistencia = {
        Hora_Proyecto: proyecto,
        Hora_Modalidad: modalidad,
        Hora_Usuario: usuario, //this.usuario.key,
        Hora_Cantidad: cantidad,
        Hora_Tiempo: tiempo,
      };
      return asignacionPersistencia;
    },

    saveAsignation(asignacion) {
      console.log("Entro");
      var saveAsignation = this.armarPersistencia();
      console.log(asignacion);
      axios
        .post(ip+"/horas/", asignacion)
        .then((response) => {
          console.log(response);
        });
    },

    generateJSONAsignacion() {
      var asignacion = {
        name: "",
        key_name: "",
        modalidades: {
          local: {
            name: "",
            local_key: "",
            horas: [],
          },
          homeoffice: {
            name: "",
            homeoffice_key: "",
            horas: [],
          },
          extranjero: {
            name: "",
            extranjero_key: "",
            horas: [],
          },
        },
      };
      for (var i = 0; i < this.days; i++) {
        asignacion.modalidades.local.horas.push(0);
        asignacion.modalidades.homeoffice.horas.push(0);
        asignacion.modalidades.extranjero.horas.push(0);
      }
        console.log(asignacion.modalidades.local.horas.length);
        console.log(asignacion.modalidades.homeoffice.horas.length);
        console.log(asignacion.modalidades.extranjero.horas.length);

      return asignacion;
    },

    asignarDataBack(){
      var asignacionI = []
      var asignacion = []
      var cantidadDeProyectos = this.cantidadDeProyectos()
       console.log(cantidadDeProyectos);
        for(var i = 0; i < cantidadDeProyectos.length; i++){
        asignacionI= this.dataGet.filter(reg => reg.Hora_Proyecto == cantidadDeProyectos[i])
        asignacion.push(asignacionI)
        asignacionI = [];
      }
      return asignacion
    },

    asignarData(){
      var asignacionesBack = this.asignarDataBack();
      var asignacion ; 
      for (var i = 0 ; i < asignacionesBack.length ; i++){

        asignacion = this.generateJSONAsignacion();
        this.armarEstructura(asignacion,asignacionesBack[i])
     } 
       this.reload()
     },
     
     armarEstructura(asignacion,lista){
      asignacion.name = lista[0].Proyecto.Proyecto_Descripcion
      asignacion.key_name = lista[0].Hora_Proyecto
      //1 local - 2 home - 3 extranjero
      var local = lista.filter( hora => hora.Hora_Modalidad == 1)
      var homeoficce = lista.filter( hora => hora.Hora_Modalidad == 2)
      var extranjero =  lista.filter(hora => hora.Hora_Modalidad == 3)
      for ( var i = 0 ; i < local.length ; i ++){
        //asignacion de horas Local
        asignacion.modalidades.local.name = local[i].Modalidad.Modalidad_Descripcion
        asignacion.modalidades.local.local_key = local[i].Hora_Modalidad
        asignacion.modalidades.local.horas[i] = local[i].Hora_Cantidad

        // asignacio de horas Home
        asignacion.modalidades.homeoffice.name = homeoficce[i].Modalidad.Modalidad_Descripcion
        asignacion.modalidades.homeoffice.homeoffice_key = homeoficce[i].Hora_Modalidad
        asignacion.modalidades.homeoffice.horas[i] = homeoficce[i].Hora_Cantidad

        //asignacio de horas Extranjeros
        asignacion.modalidades.extranjero.name = extranjero[i].Modalidad.Modalidad_Descripcion
        asignacion.modalidades.extranjero.extranjero_key = extranjero[i].Hora_Modalidad
        asignacion.modalidades.extranjero.horas[i] = extranjero[i].Hora_Cantidad
      }

      console.log(asignacion)
      this.asignacionesHoras.push(asignacion)
      this.reload()
},
  
    // ------------ METODOS PARA BEFOREMOUNT ------------ //

    // Me traigo las modalidades de la base
    giveMeModalidades() {
      axios.get(ip+"/modalidades/").then((response) => {
       this.modalidadesDescripciones = response.data.map(modalidad => modalidad.Modalidad_Descripcion);
       this.modalidades = response.data;
      })
      console.log(this.modalidadesDescripciones);
    },

    //me traigo los proyectos del back que tenga la "X" en visible.
    giveMeProyects() {
      axios.get(ip+"/proyectos/").then((response) => {
        console.log(response.data);
        for (var i = 0; i <= response.data.length; i++) {
          var proyect = {
            proyecto_key :response.data[i].Proyecto_Key,
            proyecto_description : response.data[i].Proyecto_Descripcion
          };
          console.log(response.data[i])
          this.proyectos.push(proyect);
          this.proyectosDescripciones.push(
            response.data[i].Proyecto_Descripcion,
            // response.data[i].Proyecto_Key
          );
        }
      });
    },

    //Me traigo los usuarios del back .
    giveUser() {
      axios.get(ip+"/usuarios/").then((response) => {
       this.usuariosDescripciones = response.data.map(usuario => usuario.Usuario_Nombre_Completo);
       this.usuarios = response.data;
      })
    },

    //Me traigo las horas por defecto , Mes actual y usuario conectado.
    async giveMeHours() {
      this.MesActualFormato();
      this.asignacionesHoras = [];
     await axios.get(ip+"/horas/"+this.usuarioSeleccionado +"/"+this.mesActualDinamico)
        .then((response) => {
          console.log(response.data);
          this.dataGet = response.data;
        });
      this.asignarData();  
      console.log("pase");
      this.reload()
    },

    cantidadDeProyectos() {
      var cantidad = [];
      cantidad = this.dataGet.map((json) => json.Hora_Proyecto);
      cantidad = new Set(cantidad);
      cantidad = Array.from(cantidad);
      
      return cantidad;
    },

    meterNumerosLocal() {
      var valorInput = document.getElementById("horas").value;
      for (var i = 1; i <= asignacionDev1.modalidades.local.horaslength; i++) {
        var valor = document.createElement("p");
        valor.innerHTML = valorInput + " posicion " + i;
      }
    },

    //Para hacer los pedidos necesarios al back antes de montar el componente
    beforeMount() {
      this.giveMeProyects();
      this.giveUser();
      //this.giveMeHours();
    },
  },

  mounted() {
    this.giveMeProyects()
    this.giveMeHours();
    this.giveUser();
    
  },

  beforeUpdate() {
    // Traduccion de MomentJs
    moment.locale("es");
    var localLocale = moment().locale("es");
    localLocale.locale("es");
    
    moment.updateLocale("es", {
    weekdays : 'domingo_lunes_martes_miercoles_jueves_viernes_sabado'.split('_'),
    weekdaysShort : 'dom._lun._mar._mie._jue._vie._sab.'.split('_'),
    weekdaysMin : 'Do_Lu_Ma_Mi_Ju_Vi_Sa'.split('_'),
    });

    // Dias en numeros del Mes seleccionado
    this.days = moment(this.MesSeleccionado).daysInMonth();

    // Datos para resolver => moment.weekdays() \\ Trae valores de los dias de semana en espaol.
    // Dias completos del mes actual
    const Mes = Array.from(
      { length: moment(this.MesSeleccionado).locale("es").daysInMonth() },
      (x, i) => moment(this.MesSeleccionado).startOf("month").add(i, "days")
    );

    // Dias en letra del Mes seleccionado
    this.dayName = Mes.map((d) => String(d._d).substr(0, 1));
  },
};
</script>

